import React from 'react'
import { Row, Col, Modal } from 'antd'
import styled from 'styled-components'
import closeIconImg from '../../../images/btn_close_purp.png'


const modalStyle = {
}
const bodyStyle = {
  padding: 0,
}
export default class extends React.Component {
  render () {
    const { title, subtitle, onClose, bgImg, bodyImg, textArr, closeImg } = this.props
    const span = textArr.length > 0 ? (24 / Math.floor(textArr.length)) : 24
    const textNode = textArr.map((obj, index) => {
      let innerTextNode = <ItemText>{obj.text}</ItemText>
      if (Array.isArray(obj.text)) {
        innerTextNode = obj.text.map((v, i) => <ItemText key={i}>{v}</ItemText>)
      }
      return (
        <Item span={span} key={index} bgcolor={obj.bgcolor}>
          <ItemTitle>{obj.title}</ItemTitle>
          {innerTextNode}
        </Item>
      )
    })
    const body = bodyImg ? <Body src={bodyImg} /> : <div style={{ height: 250 }} />
    return (
      <Modal
        visible={this.props.visible}
        onCancel={this.props.onClose}
        footer={null}
        width={700}
        closable={false}
        style={modalStyle}
        bodyStyle={bodyStyle}
      >
        <Container bgImg={bgImg}>
          <Title>
            {title}
            <SubTitle>{subtitle}</SubTitle>
          </Title>
          <Icon src={closeImg || closeIconImg} onClick={onClose} />
          {body}
          <ListContainer>
            <Row type="flex" justify="space-between" gutter={8}>
              {textNode}
            </Row>
          </ListContainer>
        </Container>
      </Modal>
    )
  }
}

const Container = styled.div`
  position: relative;
  background: url(${props => props.bgImg}) no-repeat;
  background-size: cover;
  padding: 30px;
  color: white;
`
const Title = styled.h2`
  font-size: 30px;
  font-weight: lighter;
  color: white;
`
const SubTitle = styled.span`
  font-size: 14px;
  font-weight: lighter;
  margin-left: 20px;
`
const Body = styled.img`
  margin: 30px 5px;
  width: 630px;
`

const ListContainer = styled.div`
  max-width: 700px;
  margin: auto;
`
const Item = styled(Col)`
  position: relative;
  color: white;
  text-align: center;
  max-width: 210px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background-color: ${props => props.bgcolor || 'none'};
  padding: 15px;
`
const Icon = styled.img`
  position: absolute;
  top: 20px;
  right: 20px;
  width: 35px;
  cursor: pointer;
`
const ItemTitle = styled.div`
  font-size: 12px;
  margin-bottom: 10px;

`
const ItemText = styled.div`
  font-size: 10px;
  margin-bottom: 0.8em;
`



