import React from 'react'
import { Button } from 'antd'
import styled from 'styled-components'
import TitleWithLine from '../common/TitleWithLine'
import crLogoImg from '../../../images/cr_logo.png'
import crBgImg from '../../../images/cr_bg.png'
import crMobileBgImg from '../../../images/mobile/cr_bg.png'
import crLineImg from '../../../images/cr_title_line.png'
import { text, bg } from '../../constants/color'

import { breakPoint } from '../../constants/variable'

export default class extends React.Component {
  render () {
    return (
      <Container className="container">
        <Inner className="container-inner">
          <StyledHeader label="INTRODUCING" titleColor="navy" line={crLineImg} />
          <StyledImg src={crLogoImg} />
          <SubTitle>cyber republic</SubTitle>
          <Desc>A decentralized governance model governed by the community and council.</Desc>
          <StyledBtn type="round" size="large" href="https://cyberrepublic.org" target="_blank" style={{ fontSize: 12 }}>GET REWARDED TODAY</StyledBtn>
        </Inner>
      </Container>
    )
  }
}


const Container = styled.div`
  background: url(${crBgImg}) no-repeat;
  background-color: #4cd3a2;
  background-size: cover;
  background-position-x: right;
  background-position-y: center;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    background-image: url(${crMobileBgImg});
  }
`
const Inner = styled.div`
  position: relative;
  text-align: left;
  padding: 60px 80px;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    padding: 50px 20px;
  }
`
const StyledHeader = styled(TitleWithLine)`
  margin-bottom: 15px;
  margin-left: 80px;
`

const StyledImg = styled.img`
  width: 50px;
  position: absolute;
  top: 90px;
  left: 0;

  @media only screen and (max-width: ${breakPoint.mobile}) {
    position: initial;
    top: 0;
    display: block;
    margin-top: 20px;
  }
`
const StyledBtn = styled(Button)`
  background-color: ${bg.dark};
  color: white;
  border: none;

  @media only screen and (max-width: ${breakPoint.mobile}) {
    font-size: 16px;
    font-weight: 300;
    padding: 12px 24px;
    box-sizing: content-box;
    line-height: 1.0;
  }
`

const Desc = styled.p`
  font-size: 14px;
  margin-top: 20px;

  @media only screen and (max-width: ${breakPoint.mobile}) {
    font-size: 24px;
  }
`
const SubTitle = styled.h2`
  font-size: 36px;
  display: inline;
  font-weight: lighter;
  color: ${text.navy};
`



