import React from 'react'
import { Row, Col } from 'antd'
import styled from 'styled-components'
import headerBgImg from '../../images/team-bg@2x.png'
import { text, bg } from '../constants/color'

import { breakPoint } from '../constants/variable'

export default class extends React.Component {
  render () {
    return (
      <Container>
        <BgRight src={headerBgImg} />
        <Inner>
          <Title>Elastos Roadmap</Title>

          <br/>
          <br/>

          2019 Roadmap Coming Soon<br/>
          <br/>
          For a recap of our 2018 accomplishments see our <a href="https://news.elastos.org/wp-content/uploads/2019/01/Elastos-EOY-Report-2018.pdf" target="_blank">2018 End of Year Report</a>
        </Inner>
      </Container>
    )
  }
}


const Container = styled.div`
  position: relative;
`
const BgRight = styled.img`
  position: absolute;
  right: 0;
  top: -25px;
  width: 450px;
  z-index: -1;
`
const Inner = styled.div`
  max-width: 950px;
  margin: 0 auto;
  padding: 100px 0;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    padding-top: 140px;
  }
`
const Title = styled.div`
  font-size: 64px;
  letter-spacing: -0.02em;
  color: ${text.navy};
  
  @media only screen and (max-width: ${breakPoint.mobile}) {

  }
`



