import React from 'react'
import Modal from './Modal'
import bodyImg from '../../../images/bucket_runtime_info.png'
import closeImg from '../../../images/btn_close_green.png'
import bgImg from '../../../images/bucket_carrier_bg.png'

const title = 'Elastos Runtime'
const subtitle = 'Secure Platform for the Modern Internet'
const textArr = [
  {
    title: 'Phoase 0 - Decentralized API - NOW',
    text: 'Simple, easy-to-use APIs to port existing apps to the Elastos Ecosystem. A wide variety of services are continually being added by partners and the Cyber Republic initiative',
  },
  {
    title: 'Phase 1 - Elastos Trinity - 2019',
    text: [
      'An official framework built on Ionic, which is bundled with SDKs, plugins and a built-in app launcher for quicl dApp development.',
      'Access to decentralized services, such as Elastos Hive IPFS mean you can create real dApps with minimal blockchain experience.',
    ],
  },
  {
    title: 'Phase 2 - Elastos Runtime - 2020',
    text: 'With code from development for over 18 years, a revolutionary runtime provides a high performance secure sandbox for dApps. Network traffic is restricted through the Elastos Carrier network only, thus achieving the goal of a true decentralized network that is resistant to attacks.',
  },
  {
    title: 'Phase 3 - Elastos TEE - 2020+',
    text: [
      'The trusted execution environment(TEE) further secures Elastos dApps from malicious actors and opens the doors to a myriad of potential applicatons.',
      'A hardware root of trust ensures the entire stack is verified by the blockchain.',
    ],
  },
]
export default class extends React.Component {
  render () {
    const { onClose, visible } = this.props
    const props = {
      visible,
      title,
      subtitle,
      onClose,
      textArr,
      closeImg,
      undefined,
      bgImg,
    }
    return (
      <Modal {...props} />
    )
  }
}



