import React from 'react'
import { Row, Col } from 'antd'
import styled from 'styled-components'
import MediaQuery from 'react-responsive'
import Partner from './partner'
import TitleWithLine from '../common/TitleWithLine/index'
import ModalEco from './ModalEco'
import ModalDid from './ModalDid'
import ModalDma from './ModalDma'
import ModalHive from './ModalHive'
import { breakPoint } from '../../constants/variable'
// import lineImage from '../../../images/ecosystem_title_line.png'
import expandIconImage from '../../../images/ecosystem_expand_btn.png'
import bodyImage from '../../../images/ecosystem_bg.png'
import mobileBG from '../../../images/mobile/ecosystem_bg.png'
import hiveImg from '../../../images/circle_hive.png'
import didImg from '../../../images/circle_did.png'
import dmaImg from '../../../images/circle_dma.png'
import ecoImg from '../../../images/circle_eco.png'

import bgItemBlockchain from '../../../images/circle_purp.png'
import bgItemEth from '../../../images/circle_tan.png'
import bgItemRuntime from '../../../images/circle_green.png'
import bgItemCarrier from '../../../images/circle_blue.png'

import '../../common.less'

export default class extends React.Component {
  state = {
    showEco: false,
    showHive: false,
    showDma: false,
    showDid: false,
  }
  render () {
    const { showEco, showHive, showDma, showDid } = this.state
    return (
      <Container className="container">
        <Inner className="container-inner">
          <Header type="flex" justify="space-around" gutter={{ xs: 8, lg: 16 }}>
            <Col xs={24} sm={12}>
              <TitleWithLine title="Ecosystem" />
            </Col>
            <Col xs={24} sm={12}>
              <StatList type="flex" justify="space-around" gutter={{ xs: 8, lg: 16 }}>
                <StatItem xs={24} sm={8}>
                  <StatItemTitle>1M</StatItemTitle>
                  <StatItemText>Carrier Nodes</StatItemText>
                </StatItem>
                <StatItem xs={24} sm={8}>
                  <StatItemTitle>300K</StatItemTitle>
                  <StatItemText>DIDs</StatItemText>
                </StatItem>
                <StatItem xs={24} sm={8}>
                  <StatItemTitle>20+</StatItemTitle>
                  <StatItemText>Projects/Devs</StatItemText>
                </StatItem>
              </StatList>
            </Col>
          </Header>
          <MediaQuery minWidth={950}>
            <Body src={bodyImage} />
            <EcoExpandIcon src={expandIconImage} type="blockchain" onClick={this.toggleEco}/>
            <HiveExpandIcon src={expandIconImage} type="eth" onClick={this.toggleHive}/>
            <DidExpandIcon src={expandIconImage} type="runtime" onClick={this.toggleDid}/>
            <DMAExpandIcon src={expandIconImage} type="carrier"  onClick={this.toggleDma}/>
          </MediaQuery>
          {/*
          <MediaQuery minWidth={breakPoint.mobile}>
            <Body src={ecoImg} onClick={this.toggleEco} />
            <Body src={hiveImg} onClick={this.toggleHive} />
            <Body src={didImg} onClick={this.toggleDid} />
            <Body src={dmaImg} onClick={this.toggleDma} />
          </MediaQuery>
          */}
          <MediaQuery maxWidth={950}>
            <ListContainer>
              <Row type="flex" justify="space-around" gutter={{ xs: 8, lg: 24 }}>
                <Item xs={24} md={6} type="blockchain" onClick={this.toggleEco}>
                  <ItemContent>
                    <ItemTitle>ELASTOS <br />ECOSYSTEM</ItemTitle>
                    <Icon src={expandIconImage} type="expand"/>
                    <ItemText>Hybrid AuxPoW/PoS <br />Consenses</ItemText>
                  </ItemContent>
                </Item>
                <Item xs={24} md={6} type="eth" onClick={this.toggleHive}>
                  <ItemContent>
                    <ItemTitle>ELASTOS <br />HIVE(IPFS)</ItemTitle>
                    <Icon src={expandIconImage} type="expand"/>
                    <ItemText>Solidity Smart Contracts <br />at 500-1500+ TPS</ItemText>
                  </ItemContent>
                </Item>
                <Item xs={24} md={6} type="runtime" onClick={this.toggleDma} >
                  <ItemContent>
                    <ItemTitle>ELASTOS <br />BIZFRAMEWORK(DMA)</ItemTitle>
                    <Icon src={expandIconImage} type="expand"/>
                    <ItemText>Secure Platform for the <br />Modern Internet</ItemText>
                  </ItemContent>
                </Item>
                <Item xs={24} md={6} type="carrier" onClick={this.toggleDid}>
                  <ItemContent>
                    <ItemTitle>ELASTOS <br />DID</ItemTitle>
                    <Icon src={expandIconImage} type="expand"/>
                    <ItemText>Blockchain Powered <br />Secure P2P Network</ItemText>
                  </ItemContent>
                </Item>
              </Row>
            </ListContainer>
          </MediaQuery>

          {/*
            <Body src={mobileBG} />

          */}

          <Partner />
        </Inner>
        <ModalEco visible={showEco} onClose={this.toggleEco} />
        <ModalHive visible={showHive} onClose={this.toggleHive} />
        <ModalDma visible={showDma} onClose={this.toggleDma} />
        <ModalDid visible={showDid} onClose={this.toggleDid} />
      </Container>
    )
  }

  toggleEco = () => {
    const { showEco } = this.state
    this.setState({ showEco: !showEco })
  }
  toggleHive = () => {
    const { showHive } = this.state
    this.setState({ showHive: !showHive })
  }
  toggleDma = () => {
    const { showDma } = this.state
    this.setState({ showDma: !showDma })
  }
  toggleDid = () => {
    const { showDid } = this.state
    this.setState({ showDid: !showDid })
  }
}

const Container = styled.div`
`
const Inner = styled.div`
  position: relative;
  padding-top: 60px;
  `
const Header = styled(Row)`
  @media only screen and (max-width: ${breakPoint.mobile}) {
    padding-left: 20px;
    padding-right: 20px;
  }
`

const Body = styled.img`
  margin: 45px auto;
  width: 950px;
  height: 500px; 
`

const StatList = styled(Row)`
  @media only screen and (max-width: ${breakPoint.mobile}) {
    margin-top: 30px;
  }
`

const StatItem = styled(Col)`
  :not(:last-child) {
    border-right: 1px solid lightgray;
  }
  font-size: 12px;
  margin-bottom: 10px;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    :not(:last-child) {
      border-right: none;
    }
    font-size: 14px;
    text-align: left;
  }
`

const StatItemTitle = styled.div`
  font-size: 30px;
  margin-bottom: 10px;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    display: inline;
    font-size: 14px;
  }
`

const StatItemText = styled.div`
  font-size: 14px;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    display: inline;
  }
`
const ListContainer = styled.div`
  /* width: calc(100% - 300px); */
  max-width: 850px;
  margin: 45px auto;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    width: 100%;
  }
`
const Item = styled(Col)`
  position: relative;
  background-color: #0F2D3B;
  color: white;
  height: 180px;
  width: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    margin: 12px 0;
    width: 325px;
    height: 325px;
    background-position: 50% 50%;
    background-size: 100%;
  }

  background: url(
    ${props => props.type === 'blockchain' && bgItemBlockchain
      || props.type === 'eth' && bgItemEth
      || props.type === 'runtime' && bgItemRuntime
      || props.type === 'carrier' && bgItemCarrier
    }
  ) no-repeat;
  background-size: contain;
`
const ItemContent = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100%;
`
const Icon = styled.img`
  width: 35px;
  :hover {
    cursor: pointer;
  }
`

const EcoExpandIcon = styled(Icon)`
  position: absolute;
  left: 153px;
  top: 370px;
`

const HiveExpandIcon = styled(Icon)`
  position: absolute;
  left: 358px;
  top: 520px;
`

const DidExpandIcon = styled(Icon)`
  position: absolute;
  left: 555px;
  top: 370px;
`

const DMAExpandIcon = styled(Icon)`
  position: absolute;
  left: 758px;
  top: 520px;
`


const ItemTitle = styled.div`
  font-size: 12px;
  margin-bottom: 10px;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    font-size: 24px;
  }
`
const ItemText = styled.div`
  font-size: 10px;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    font-size: 16px;
  }
`



