import React from 'react'

import '../../common.less'
import './style.less'
import styled from 'styled-components'

import { images } from './images'

import { breakPoint } from '../../constants/variable'

import MediaQuery from 'react-responsive'
import { Dropdown, Menu, Icon } from 'antd'

const INDUSTRY_TYPE = {
  IDENTITY: 'IDENTITY',
  IOT: 'IOT',
  MEDIA: 'MEDIA',
  SECURITY: 'SECURITY',
  REAL: 'REAL'
}

const TYPE_TO_DISPLAY = {
  IDENTITY: 'Identity',
  IOT: 'IoT',
  MEDIA: 'Media',
  SECURITY: 'Security',
  REAL: 'Real Assets'
}

import { text, border, bg } from '../../constants/color'

export default class Hero extends React.Component {

  constructor () {
    super()
    this.state = {
      industryType: INDUSTRY_TYPE.IDENTITY
    }
  }

  render () {

    // TODO: remove this className here, use the standard container/containerInner

    return <Container className="container" industryType={this.getParentClass()}>
      <ContainerInner className="container-inner">
        <IndustryMenu>
          {/* TODO: use common/TitleWithLine */}
          <IndustryMenuTitle>
            ELASTOS
            <IndustryMenuTitleLine src={images.titleLineImg}/>
          </IndustryMenuTitle>
          <IndustryMenuSubtitle>Industries</IndustryMenuSubtitle>
          <MediaQuery minWidth={breakPoint.mobile}>

            {/* preload images */}
            <link rel="preload" href={images.identityBgImg} as="image"/>
            <link rel="preload" href={images.iotBgImg} as="image"/>
            <link rel="preload" href={images.mediaBgImg} as="image"/>
            <link rel="preload" href={images.securityBgImg} as="image"/>
            <link rel="preload" href={images.realBgImg} as="image"/>

            <link rel="preload" href={images.identityInfoImg} as="image"/>
            <link rel="preload" href={images.iotInfoImg} as="image"/>
            <link rel="preload" href={images.mediaInfoImg} as="image"/>
            <link rel="preload" href={images.securityInfoImg} as="image"/>
            <link rel="preload" href={images.realInfoImg} as="image"/>

            <IndustryMenuListContainer>
              <IndustryMenuListItem onClick={() => this.changeIndustry(INDUSTRY_TYPE.IDENTITY)} className={this.state.industryType === INDUSTRY_TYPE.IDENTITY ? 'selected' : undefined}>
                {TYPE_TO_DISPLAY.IDENTITY}
              </IndustryMenuListItem>
              <IndustryMenuListItem onClick={() => this.changeIndustry(INDUSTRY_TYPE.IOT)} className={this.state.industryType === INDUSTRY_TYPE.IOT ? 'selected' : undefined}>
                {TYPE_TO_DISPLAY.IOT}
              </IndustryMenuListItem>
              <IndustryMenuListItem onClick={() => this.changeIndustry(INDUSTRY_TYPE.MEDIA)} className={this.state.industryType === INDUSTRY_TYPE.MEDIA ? 'selected' : undefined}>
                {TYPE_TO_DISPLAY.MEDIA}
              </IndustryMenuListItem>
              <IndustryMenuListItem onClick={() => this.changeIndustry(INDUSTRY_TYPE.SECURITY)} className={this.state.industryType === INDUSTRY_TYPE.SECURITY ? 'selected' : undefined}>
                {TYPE_TO_DISPLAY.SECURITY}
              </IndustryMenuListItem>
              <IndustryMenuListItem onClick={() => this.changeIndustry(INDUSTRY_TYPE.REAL)} className={this.state.industryType === INDUSTRY_TYPE.REAL ? 'selected' : undefined}>
                {TYPE_TO_DISPLAY.REAL}
              </IndustryMenuListItem>
            </IndustryMenuListContainer>
          </MediaQuery>
          <MediaQuery maxWidth={breakPoint.mobile}>

            {/* preload images */}
            <link rel="preload" href={images.identityBgMobImg} as="image"/>
            <link rel="preload" href={images.iotBgMobImg} as="image"/>
            <link rel="preload" href={images.mediaBgMobImg} as="image"/>
            <link rel="preload" href={images.securityBgMobImg} as="image"/>
            <link rel="preload" href={images.realBgMobImg} as="image"/>

            <link rel="preload" href={images.identityInfoMobImg} as="image"/>
            <link rel="preload" href={images.iotInfoMobImg} as="image"/>
            <link rel="preload" href={images.mediaInfoMobImg} as="image"/>
            <link rel="preload" href={images.securityInfoMobImg} as="image"/>
            <link rel="preload" href={images.realInfoMobImg} as="image"/>

            <IndustryMenuListContainer>
              <MobileIndustryDropdown>
                <Dropdown overlay={this.getMobileIndustryMenu()}>
                  <a className="ant-dropdown-link">
                    {TYPE_TO_DISPLAY[this.state.industryType]}
                    <IconDown src={images.iconDownImg}/>
                  </a>
                </Dropdown>
              </MobileIndustryDropdown>
            </IndustryMenuListContainer>
          </MediaQuery>
        </IndustryMenu>
        <IndustryBody>
          <IndustryInfographic className={this.getParentClass()} src={this.getInfographic()}/>
        </IndustryBody>
      </ContainerInner>
    </Container>
  }

  getMobileIndustryMenu() {
    return (
      <Menu>
        <Menu.Item>
          <a onClick={() => this.changeIndustry(INDUSTRY_TYPE.IDENTITY)}>Identity</a>
        </Menu.Item>
        <Menu.Item>
          <a onClick={() => this.changeIndustry(INDUSTRY_TYPE.IOT)}>IoT</a>
        </Menu.Item>
        <Menu.Item>
          <a onClick={() => this.changeIndustry(INDUSTRY_TYPE.MEDIA)}>Media</a>
        </Menu.Item>
        <Menu.Item>
          <a onClick={() => this.changeIndustry(INDUSTRY_TYPE.SECURITY)}>Security</a>
        </Menu.Item>
        <Menu.Item>
          <a onClick={() => this.changeIndustry(INDUSTRY_TYPE.REAL)}>Real Assets</a>
        </Menu.Item>
      </Menu>
    )
  }

  getParentClass () {
    switch (this.state.industryType) {
      case INDUSTRY_TYPE.IDENTITY:
        return 'identity'

      case INDUSTRY_TYPE.IOT:
        return 'iot'

      case INDUSTRY_TYPE.MEDIA:
        return 'media'

      case INDUSTRY_TYPE.SECURITY:
        return 'security'

      case INDUSTRY_TYPE.REAL:
        return 'real'
    }
  }

  changeIndustry (industryType) {
    this.setState({
      industryType: industryType
    })
  }

  getInfographic() {

    if (typeof window !== 'undefined') {
      const isMobile = window.innerWidth < parseFloat(breakPoint.mobile)

      switch (this.state.industryType) {
        case INDUSTRY_TYPE.IDENTITY:
          return isMobile ? images.identityInfoMobImg : images.identityInfoImg
        case INDUSTRY_TYPE.IOT:
          return isMobile ? images.iotInfoMobImg : images.iotInfoImg
        case INDUSTRY_TYPE.MEDIA:
          return isMobile ? images.mediaInfoMobImg : images.mediaInfoImg
        case INDUSTRY_TYPE.SECURITY:
          return isMobile ? images.securityInfoMobImg : images.securityInfoImg
        case INDUSTRY_TYPE.REAL:
          return isMobile ? images.realInfoMobImg : images.realInfoImg
      }
    } else {
      switch (this.state.industryType) {
        case INDUSTRY_TYPE.IDENTITY:
          return images.identityInfoImg
        case INDUSTRY_TYPE.IOT:
          return images.iotInfoImg
        case INDUSTRY_TYPE.MEDIA:
          return images.mediaInfoImg
        case INDUSTRY_TYPE.SECURITY:
          return images.securityInfoImg
        case INDUSTRY_TYPE.REAL:
          return realInfoImg
      }
    }
  }
}

const Container = styled.div`
  margin: 0 auto;
  background-size: cover;
  height: 700px;
 
  ${props => {
    switch (props.industryType) {
      case 'identity':
        return `background-image: url(${images.identityBgImg});`
      case 'iot':
        return `background-image: url(${images.iotBgImg});`
      case 'media':
        return `background-image: url(${images.mediaBgImg});`
      case 'security':
        return `background-image: url(${images.securityBgImg});`
      case 'real':
        return `background-image: url(${images.realBgImg});`
    }
  }}
  
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility:    hidden;
  -ms-backface-visibility:     hidden;
  
  @media only screen and (max-width: ${breakPoint.mobile}) {
    height: 100%;
    
    ${props => {
    switch (props.industryType) {
      case 'identity':
        return `background-image: url(${images.identityBgMobImg});`
      case 'iot':
        return `background-image: url(${images.iotBgMobImg});`
      case 'media':
        return `background-image: url(${images.mediaBgMobImg});`
      case 'security':
        return `background-image: url(${images.securityBgMobImg});`
      case 'real':
        return `background-image: url(${images.realBgMobImg});`
      }
    }}
  }
`

const ContainerInner = styled.div`
  overflow: visible;
  
  max-width: 950px;
  height: 100%;
  margin: 0 auto;
  
  @media only screen and (max-width: ${breakPoint.mobile}) {
    padding: 24px 24px 72px 24px;
  }
`

const IndustryMenu = styled.div`
  background-color: white;
  width: 225px;
  height: 450px;
  float: left;
  text-align: left;
  margin-top: 120px;
  
  @media only screen and (max-width: ${breakPoint.mobile}) {
    float: none;
    width: 100%;
    height: auto;
    margin-top: 0;
  }
`

const IndustryMenuTitle = styled.div`
  font-weight: 400;
  font-size: 10px;

  padding: 35px 0 0 30px;
  
  color: ${text.blue};
  
  @media only screen and (max-width: ${breakPoint.mobile}) {
    font-size: 14px;
    font-weight: 300;
  }
`

const IndustryMenuTitleLine = styled.img`
  width: 100px;
  height: 1px;
  padding-left: 10px;
  
  @media only screen and (max-width: ${breakPoint.mobile}) {
    width: 200px;
  }
`

const IndustryMenuSubtitle = styled.span`
  padding-top: 24px;
  font-size: 36px;
  padding-left: 30px;
  
  color: ${text.darkBlue};
  
  @media only screen and (max-width: ${breakPoint.mobile}) {
    padding-top: 36px;
    font-size: 48px;
    font-weight: 300;
  }
`

// TODO: 1s fade?
const IndustryBody = styled.div`
  overflow-y: visible;
  overflow-x: hidden;
  padding-top: 120px;
  
  @media only screen and (max-width: ${breakPoint.mobile}) {
    width: 100%;
    padding-top: 24px;
  }
`

const IndustryInfographic = styled.img`
  width: 100%;
  padding-left: 45px;
  
  @media only screen and (max-width: ${breakPoint.mobile}) {
    padding-left: 0;
  }
`

const IndustryMenuListContainer = styled.ul`
  margin-top: 30px;
  padding-left: 0;
  border-bottom: 1px solid #cfdae4;
  
  @media only screen and (max-width: ${breakPoint.mobile}) {
    margin-bottom: 0;
    border-bottom: 0;
  }
`

const IndustryMenuListItem = styled.ul`
  border-top: 1px solid ${border.light};
  
  padding: 15px 30px;
  cursor: pointer;

  &:hover {
    background-color: ${border.light};
  }

  &.selected {
    color: ${text.white};
    background-color: ${bg.selected};
  }
`

const MobileIndustryDropdown = styled.div`
  padding: 20px 30px;
  background-color: ${bg.selected};
  
  a {
    color: ${text.white}
    font-weight: 300;
    font-size: 18px;
    width: 100%;
    display: block;
    
    i {
      float: right;
      font-size: 200%;
    }
  }
`

const IconDown = styled.img`
  float: right;
  width: 20px;
  padding-top: 10px;
`



