import React from 'react'
import styled, { css } from 'styled-components'

import MediaQuery from 'react-responsive'
import { Drawer, Dropdown, Menu, Icon } from 'antd'
import { breakPoint } from '../../constants/variable'

import logo from "../../../images/header_logo.png"
import dropdown from "../../../images/btn_dropdown.png"
import arrow from "../../../images/header_arrow_right.png"

import './style.less'

export default class App extends React.Component {
  constructor () {
    super()
    this.state = {
      drawerVisible: false
    }
  }

  render() {
    return (
      <div>
        <div className="top-header">
          <div>
            <a href="/developer">DEVELOPER ACCOUNT</a> &nbsp;
            <img src={arrow} className="arrow"/>
          </div>

          <div>
            <a href="https://cyberrepublic.org/" target="_blank">JOIN CYBER REPUBLIC</a> &nbsp;
            <img src={arrow} className="arrow"/>
          </div>

        </div>

        <div className="header">
          <div className="logo">
            <a href="/">
              <Logo src={logo}/>
            </a>
          </div>

          <MediaQuery minWidth={breakPoint.mobile}>
            <NavBar className="nav">
              <a href="https://news.elastos.org/" target="_blank">NEWS</a>
              <a href="https://blockchain.elastos.org/" target="_blank">BLOCKCHAIN</a>
              <a href="/team">TEAM</a>
              <a href="/roadmap">ROADMAP</a>
              <a href="https://www.elastos.org/downloads/elastos_whitepaper_en.pdf" target="_blank">WHITEPAPER</a>
              <Dropdown overlay={this.getLanguageMenu()}>
                <a href="#">LANGUAGE <img src={dropdown} className="lan-dropdown"/></a>
              </Dropdown>
            </NavBar>
          </MediaQuery>
          <MediaQuery maxWidth={breakPoint.mobile}>
            <DrawerButton onClick={() => this.setState({ drawerVisible: true })}>
              <Icon type="menu-fold" />
            </DrawerButton>
            <Drawer
              placement="right"
              closable={false}
              onClose={() => this.setState({ drawerVisible: false })}
              visible={this.state.drawerVisible}
            >
              {this.getMobileMenu()}
            </Drawer>
          </MediaQuery>
        </div>
      </div>
    )
  }

  getLanguageMenu() {
    return (
    <Menu>
      <Menu.Item>
        <a onClick={() => window.location.href = '/'}>English</a>
      </Menu.Item>
      <Menu.Item>
        <a onClick={() => window.location.href = '/zh'}>Chinese</a>
      </Menu.Item>
    </Menu>
    )
  }


  getMobileMenu() {
    return (
      <Menu>
        <Menu.Item>
          <a href="https://news.elastos.org/" rel="noopener noreferrer" target="_blank">NEWS</a>
        </Menu.Item>
        <Menu.Item>
          <a href="https://blockchain.elastos.org/" rel="noopener noreferrer" target="_blank">BLOCKCHAIN</a>
        </Menu.Item>
        <Menu.Item>
          <a href="/team" rel="noopener noreferrer">TEAM</a>
        </Menu.Item>
        <Menu.Item>
          <a href="/roadmap" rel="noopener noreferrer">ROADMAP</a>
        </Menu.Item>
        <Menu.Item>
          <a href="https://www.elastos.org/downloads/elastos_whitepaper_en.pdf" rel="noopener noreferrer" target="_blank">WHITEPAPER</a>
        </Menu.Item>
        <Menu.Item>
          &nbsp;
        </Menu.Item>
        <Menu.Item>
          <a href="/#" rel="noopener noreferrer">English</a>
        </Menu.Item>
        <Menu.Item>
          <a href="/zh" rel="noopener noreferrer">Chinese</a>
        </Menu.Item>
      </Menu>
    )
  }
}

const DrawerButton = styled.a`
  font-size: 32px;
  color: #0F2D3B;
`

const NavBar = styled.div`
  padding-top: 24px;
  font-size: 12px;
  font-weight: 400;
`

const Logo = styled.img`
  width: 148px;
  padding-top: 12px;
`



