import React from 'react'
import { Row, Col } from 'antd'
import styled from 'styled-components'
import PlayButtonImage from '../../../images/btn_play_video.png'
import ExpandIconImage from '../../../images/ecosystem_expand_btn.png'
import BucketBlockchain from './BucketBlockchain'
import BucketEth from './BucketEth'
import BucketRuntime from './BucketRuntime'
import BucketCarrier from './BucketCarrier'
import Video from './Video'

import bgTopLeft from '../../../images/bg_shapes_left_purple.png'
import bgMiddleLeft from '../../../images/bg_shapes_left_tan.png'
import bgBottomLeft from '../../../images/bg_shapes_left_green.png'
import bgRight from '../../../images/bg_shapes_right.png'

import bgItemBlockchain from '../../../images/box_purp.png'
import bgItemEth from '../../../images/box_tan.png'
import bgItemRuntime from '../../../images/box_green.png'
import bgItemCarrier from '../../../images/box_blue.png'

import { breakPoint } from '../../constants/variable'

export default class extends React.Component {
  state = {
    showBucketBlockchain: false,
    showBucketEth: false,
    showBucketRuntime: false,
    showBucketCarrier: false,
    showVideo: false,
  }
  render () {
    const { showBucketBlockchain, showBucketEth, showBucketRuntime, showBucketCarrier, showVideo } = this.state
    return (
      <Container>
        <Title>Build on the Modern Internet</Title>
        <LinkGroup>
          <LinkItem target="_blank" href="https://news.elastos.org/wp-content/uploads/2019/01/Elastos-EOY-Report-2018.pdf">2018 END OF YEAR REPORT</LinkItem>
          <LinkItem href="/developer">DEVELOP ON ELASTOS</LinkItem>
          <LinkItem target="_blank" href="https://www.cyberrepublic.org">GET FUNDED BY CYBER REPUBLIC</LinkItem>
          <LinkItem target="_blank" href="https://wallet.elastos.org">WALLET</LinkItem>
        </LinkGroup>
        <PlayButton src={PlayButtonImage} onClick={this.toggleVideo} />
        <ListContainer>
          <Row type="flex" justify="space-around" gutter={{ xs: 8, lg: 24 }}>
            <Item xs={24} md={6} type="blockchain" onClick={this.toggleBucketBlockchain}>
              <ItemContent>
                <Icon src={ExpandIconImage} type="expand"/>
                <ItemTitle>ELASTOS <br />BLOCKCHAIN</ItemTitle>
                <ItemText>Hybrid AuxPoW/PoS <br />Consenses</ItemText>
              </ItemContent>
            </Item>
            <Item xs={24} md={6} type="eth" onClick={this.toggleBucketEth}>
              <ItemContent>
                <Icon src={ExpandIconImage} type="expand"/>
                <ItemTitle>ELASTOS ETH <br />SIDECHAINS</ItemTitle>
                <ItemText>Solidity Smart Contracts <br />at 500-1500+ TPS</ItemText>
              </ItemContent>
            </Item>
            <Item xs={24} md={6} type="runtime" onClick={this.toggleBucketRuntime} >
              <ItemContent>
                <Icon src={ExpandIconImage} type="expand"/>
                <ItemTitle>ELASTOS <br />RUNTIME</ItemTitle>
                <ItemText>Secure Platform for the <br />Modern Internet</ItemText>
              </ItemContent>
            </Item>
            <Item xs={24} md={6} type="carrier" onClick={this.toggleBucketCarrier}>
              <ItemContent>
                <Icon src={ExpandIconImage} type="expand"/>
                <ItemTitle>ELASTOS <br />CARRIER</ItemTitle>
                <ItemText>Blockchain Powered <br />Secure P2P Network</ItemText>
              </ItemContent>
            </Item>
          </Row>
        </ListContainer>
        <BGTopLeft />
        <BGMiddleLeft />
        <BGBottomLeft />
        <BGRight />
        <BucketBlockchain visible={showBucketBlockchain} onClose={this.toggleBucketBlockchain} />
        <BucketEth visible={showBucketEth} onClose={this.toggleBucketEth} />
        <BucketRuntime visible={showBucketRuntime} onClose={this.toggleBucketRuntime} />
        <BucketCarrier visible={showBucketCarrier} onClose={this.toggleBucketCarrier} />
        <Video visible={showVideo} onClose={this.toggleVideo} />
      </Container>
    )
  }
  toggleBucketBlockchain = () => {
    const { showBucketBlockchain } = this.state
    this.setState({ showBucketBlockchain: !showBucketBlockchain })
  }
  toggleBucketEth = () => {
    const { showBucketEth } = this.state
    this.setState({ showBucketEth: !showBucketEth })
  }
  toggleBucketRuntime = () => {
    const { showBucketRuntime } = this.state
    this.setState({ showBucketRuntime: !showBucketRuntime })
  }
  toggleBucketCarrier = () => {
    const { showBucketCarrier } = this.state
    this.setState({ showBucketCarrier: !showBucketCarrier })
  }
  toggleVideo = () => {
    const { showVideo } = this.state
    this.setState({ showVideo: !showVideo })
  }
}

const Container = styled.div`
  position: relative;
  margin: 80px 0 100px;
  background-color: white;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    overflow: hidden;
  }
  `
const Title = styled.h1`
  font-size: 50px;
  font-weight: 300;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    font-size: 42px;
  }
`
const BGTopLeft = styled.div`
  position: absolute;
  left: 0;
  top: 80px;
  width: 70px;
  height: 70px;
  background: url(${bgTopLeft}) no-repeat;
  background-size: contain;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    margin-left: -38px;
    margin-top: 70px;
    width: 48px;
    height: 48px;
  }
`
const BGMiddleLeft = styled.div`
  position: absolute;
  left: 70px;
  top: 150px;
  width: 70px;
  height: 70px;
  background: url(${bgMiddleLeft}) no-repeat;
  background-size: contain;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    margin-left: -60px;
    margin-top: 48px;
    width: 48px;
    height: 48px;
  }
`
const BGBottomLeft = styled.div`
  position: absolute;
  left: 0;
  top: 350px;
  width: 60px;
  height: 60px;
  background: url(${bgBottomLeft}) no-repeat;
  background-size: contain;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    margin-left: 0;
    width: 30px;
    height: 30px;
    top: 280px;
  }
`
const BGRight = styled.div`
  position: absolute;
  right: 0;
  top: 80px;
  width: 124px;
  height: 248px;
  background: url(${bgRight}) no-repeat;
  background-size: contain;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    margin-top: 70px;
    width: 54px;
  }
`
const LinkGroup = styled.div`
  display: flex;
  justify-content: space-around;
  width: 600px;
  margin: auto;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    flex-direction: column;
    width: auto;
  }
`
const LinkItem = styled.a`
  font-size: 12px;
  text-decoration: underline;
  color: #0F2D3B;
  font-weight: lighter;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    margin: 8px 0;
    font-size: 16px;
  }
`
const PlayButton = styled.img`
  height: 70px;
  margin: 30px auto;
  &:hover {
    cursor: pointer;
  }
`
const ListContainer = styled.div`
  width: calc(100% - 300px);
  max-width: 850px;
  margin: auto;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    width: 100%;
  }
`
const Item = styled(Col)`
  position: relative;
  background-color: #0F2D3B;
  color: white;
  height: 180px;
  width: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    margin: 12px 0;
    width: 325px;
    height: 325px;
    background-position: 50% 50%;
    background-size: 100%;
  }

  background: url(
    ${props => props.type === 'blockchain' && bgItemBlockchain
      || props.type === 'eth' && bgItemEth
      || props.type === 'runtime' && bgItemRuntime
      || props.type === 'carrier' && bgItemCarrier
    }
  ) no-repeat;
  background-size: contain;
`
const ItemContent = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100%;
`
const Icon = styled.img`
  position: absolute;
  top: 15px;
  right: 10px;
  width: 35px;
  :hover {
    cursor: pointer;
  }
`
const ItemTitle = styled.div`
  font-size: 12px;
  margin-bottom: 10px;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    font-size: 24px;
  }
`
const ItemText = styled.div`
  font-size: 10px;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    font-size: 16px;
  }
`



