import React from 'react'
import { Row, Col } from 'antd'
import styled from 'styled-components'
import HeaderWithLine from '../common/HeaderWithLine'
import { breakPoint } from '../../constants/variable'
import { bg } from '../../constants/color'

// import lineImage from '../../../images/elastos_partners_line.png'
import partner1 from '../../../images/logo_topnetwork.png'
import partner2 from '../../../images/logo_uptick.png'
import partner3 from '../../../images/logo_wachsman.png'
import partner4 from '../../../images/logo_ioex.png'
import { breakPoint } from '../../constants/variable'
// import partner5 from '../../../images/logo_bitmain.png'

export default class extends React.Component {
  render () {
    return (
      <Container>
        <StyledHeader title="ELASTOS PARTNERS" />
        <Row type="flex" justify="space-between" gutter={24}>
          <StatItem xs={11} md={4}>
            <StyledImg src={partner1} />
          </StatItem>
          <StatItem xs={11} md={4}>
            <StyledImg src={partner2} />
          </StatItem>
          <StatItem xs={11} md={4}>
            <StyledImg src={partner3} />
          </StatItem>
          <StatItem xs={11} md={4}>
            <StyledImg src={partner4} />
          </StatItem>
        </Row>
      </Container>
    )
  }
}


const Container = styled.div`
  padding: 50px 0;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    padding: 40px 30px;
    background-color: ${bg.lightGray};
  }
`
const StyledHeader = styled(HeaderWithLine)`
  margin-bottom: 15px;
`
const StyledImg = styled.img`

  box-sizing: border-box;
  max-height: 75px;

  @media only screen and (max-width: ${breakPoint.mobile}) {
    
    padding: 12px 24px;
    max-width: 100%;
    max-height: 75px;
  }
`

const StatItem = styled(Col)`
  font-size: 12px;
  margin-bottom: 10px;
  @media only screen and (max-width: ${breakPoint.mobile}) {
    padding: 10px;
  }
`



