import React from 'react'
import { Row, Col, Modal } from 'antd'
import styled from 'styled-components'
import closeImg from '../../../images/btn_close_purp.png'
import VideoPlayer from '../common/VideoPlayer'
import videoMp4 from '../../../video/Elastos.mp4'
import videoOgv from '../../../video/Elastos.ogv'
import videoWebm from '../../../video/Elastos.webm'

const videoJsOptions = {
  autoplay: true,
  controls: true,
  sources: [
    {
      src: videoMp4,
      type: 'video/mp4',
    },
    {
      src: videoOgv,
      type: 'video/ogg',
    },
    {
      src: videoWebm,
      type: 'video/webm',
    },
  ],
}

const modalStyle = {
  position: 'fixed',
  top: 0,
  left: 0,
  width: '100%',
  height: '100%',
  backgroundColor: '#172d39',
  opacity: 1,
  zIndex: 1,
}
const bodyStyle = {
  padding: 0,
}
export default class extends React.Component {
  render () {
    const { visible, onClose } = this.props
    return (
      <Modal
        visible={visible}
        onCancel={onClose}
        footer={null}
        width={'100%'}
        closable={false}
        style={modalStyle}
        bodyStyle={bodyStyle}
      >
        <Container>
          <Inner>
            <VideoPlayer {...videoJsOptions} onClose={onClose} />
          </Inner>
        </Container>
      </Modal>
    )
  }
}

const Container = styled.div`
  width: 100%;
  height: 100%;
  position: fixed;
  padding: 25px;
  -webkit-transform: translateZ(0);
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  z-index: 30;
`
const Inner = styled.div`
  position: relative;
  width: 100%;
  /* max-width: 1280px; */
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
`



