import React from 'react'
import Modal from './Modal'
import bodyImg from '../../../images/bucket_blockchain_info.png'
import closeImg from '../../../images/btn_close_green.png'
import bgImg from '../../../images/bucket_runtime_bg.png'

const title = 'Elastos Ecosystem'
const subtitle = 'A full suite of services for dApp developers'
const textArr = [
  {
    title: 'Elastos BizFramework(DMA)',
    text: [
      '',
      ''
    ],
  },

]
export default class extends React.Component {
  render () {
    const { onClose, visible } = this.props
    const props = {
      visible,
      title,
      subtitle,
      onClose,
      textArr,
      closeImg,
      undefined,
      bgImg,
    }
    return (
      <Modal {...props} />
    )
  }
}



